import { reactive } from 'vue'
import { startCase, cloneDeep, set } from 'lodash'

const state = reactive({
  stories: {},
  bySlug: {},
  did: {},
  slugs: [],
  count: 0,
  tree: {},
})

const doStory = (slug) => {
  if (!state.bySlug[slug]) {
    throw 'Unregistered story: ' + slug
  }
  state.did[slug] = state.did[slug] + 1
}

let i = 20

const register = (obj, path=[]) => {
  console.log(i, obj, path)
  if (Array.isArray(obj)) {
    obj.map(o => register(o, [...path]))
  } else if (typeof obj === 'string') {
    const slug = obj
    obj = {
      slug,
      name: startCase(slug),
      path: [...path, slug],
    }
    state.slugs.push(slug)
    state.bySlug[slug] = obj
    state.did[slug] = 0
    set(state.tree, obj.path, obj)
  } else {
    Object.entries(obj).forEach(([key, value]) => {
      register(value, [...path, key])
    })
  }
}

window.$state = state

export default {
  state,
  doStory,
  register,
  wrap(name, func) {
    return function() {
      doStory(name)
      return func(...arguments)
    }
  },
}
